import React from 'react';
import { Route, Routes, useLocation, useNavigate } from 'react-router';

import FormOrderTariff from '../components/form-order-tariff/FormOrderTariff';
import Modal from '../components/modal/Modal';
import ModalContent from '../components/modal/ModalContent';
import { CityContext } from '../context/CityContext';
import { useModal } from '../hooks/useModal';
import LayoutPage from '../layout-page/LayoutPage';
import SamHome from '../pages/home/SamHome';
import { NotFoundPage } from '../pages/not-found/NotFoundPage';
import SpbHome from '../pages/spb/SpbHome';
import { ROUTES } from '../utils/routes';

const AppRoutes = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const { closeModal, isModalOpen } = useModal();

  const onCloseModal = () => {
    closeModal();
    navigate(-1);
  };

  const background =
    location.state?.bgTariff || location.state?.bgTariffSpb || location;

  return (
    <>
      <Routes location={background}>
        <Route path={ROUTES.SAM.URL} element={<LayoutPage />}>
          <Route index element={<SamHome />} />
          <Route path={ROUTES.SPB.URL} element={<SpbHome />} />
          <Route path={ROUTES.URL_ANY} element={<NotFoundPage />} />
        </Route>
      </Routes>

      {location.state?.bgTariff && (
        <Routes>
          <Route
            path={`${ROUTES.SAM.URL}/:id`}
            element={
              <CityContext.Provider value={ROUTES.SAM.NAME}>
                <Modal onClose={onCloseModal} title="">
                  <ModalContent>
                    {!isModalOpen ? <FormOrderTariff /> : null}
                  </ModalContent>
                </Modal>
              </CityContext.Provider>
            }
          />
        </Routes>
      )}
      {location.state?.bgTariffSpb && (
        <Routes>
          <Route
            path={`${ROUTES.SPB.URL}/:id`}
            element={
              <CityContext.Provider value={ROUTES.SPB.NAME}>
                <Modal onClose={onCloseModal} title="">
                  <ModalContent>
                    {!isModalOpen ? <FormOrderTariff /> : null}
                  </ModalContent>
                </Modal>
              </CityContext.Provider>
            }
          />
        </Routes>
      )}
    </>
  );
};

export default AppRoutes;
