import React, { useContext, useState } from 'react';
import { NavLink } from 'react-router-dom';

import { CityContext } from '../../context/CityContext';
import { CITIES_ROUTERS_SHORT } from '../../utils/routes';
import styles from './styles.module.css';

interface ICities {
  url: string;
  name: string;
}

export default function CitiesHeader() {
  const pageCity = useContext(CityContext);
  const CITIES: ICities[] = CITIES_ROUTERS_SHORT;
  const [isOpen, setIsOpen] = useState(false);

  const classMenu =
    'z-20 overflow-y-auto max-h-[500px] text-secondary-950 z-10 absolute top-100 right-0 bg-white p-4 pt-6 rounded-lg shadow w-64 m-2 text-center uppercase';

  const onClickPoint = (): void => {
    setIsOpen(false);
  };

  return (
    <div className="relative font-bold">
      <button
        id="dropdownHoverButton"
        data-dropdown-toggle="dropdownHover"
        data-dropdown-trigger="hover"
        className={`${styles.button} sm:text-md focus:ring-4 focus:outline-none focus:ring-blue-300 rounded-lg pr-0 sm:px-5 py-2.5 text-center inline-flex items-center`}
        type="button"
        onClick={() => setIsOpen(prev => !prev)}
      >
        {pageCity}{' '}
        <svg
          className="w-2.5 h-2.5 ml-2.5"
          aria-hidden="true"
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 10 6"
        >
          <path
            stroke="currentColor"
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth="2"
            d="m1 1 4 4 4-4"
          />
        </svg>
      </button>

      <div
        id="dropdownHover"
        className={`${classMenu} ${!isOpen ? 'hidden' : ''}`}
      >
        <h3> </h3>
        <ul className="py-2 mt-2 text-sm" aria-labelledby="dropdownHoverButton">
          {CITIES.length > 0 &&
            [...CITIES].map((city: ICities, index) => (
              <li key={index}>
                <NavLink
                  to={city.url}
                  className="block p-4 mb-3 text-center text-secondary-950 border-2 border-secondary-950 rounded-lg hover:text-secondary-950"
                  onClick={onClickPoint}
                >
                  {city.name}
                </NavLink>
              </li>
            ))}
        </ul>
      </div>
    </div>
  );
}
