import React from 'react';

import { IconCheckCircle } from '../icons';
import { TARIFF_LIST, TARIFF_LIST_2 } from './data';

interface ITariffList {
  isPool: boolean;
}

export default function TariffList({ isPool }: ITariffList) {
  const data = isPool ? TARIFF_LIST : TARIFF_LIST_2;
  return (
    <ul className="pt-4 md:pt-8">
      {[...data].map(el => (
        <li key={el} className="flex items-baseline mb-2 relative">
          <IconCheckCircle
            width={17}
            height={16}
            className="absolute left-0 top-0 w-5 md:w-6 h-4 md:h-5 text-primary"
          />
          <span className="w-full pb-1 pl-6 md:pl-8 text-sm md:text-md">
            {el}
          </span>
        </li>
      ))}
    </ul>
  );
}
