import { ROUTES } from './routes';

export let BASE_URL = process.env.REACT_APP_BASE_URL;
export let BASE_NAME = process.env.REACT_APP_BASENAME;

if (process.env.REACT_APP_VERSION === 'dev') {
  BASE_URL = process.env.REACT_APP_URL_DEV;
}
if (process.env.REACT_APP_VERSION === 'deploy') {
  BASE_URL = process.env.REACT_APP_BASE_URL_DEPLOY;
  BASE_NAME = process.env.REACT_APP_BASENAME_DEPLOY;
}

export const SPB_CITY_DATA = {
  theme: '',
  city: '-',
  clubs: [
    {
      address: '.  .., . 68',
      name: '',
    },
  ],
  timer: {
    endDate: 'December, 15, 2023',
    startDate: '',
  },
};

export const getDataList = (city, sam, spb) => {
  if (city === ROUTES.SAM.NAME) {
    return sam;
  } else if (city === ROUTES.SPB.NAME) {
    return spb;
  }
};
