import 'swiper/css/navigation';
import 'swiper/css/scrollbar';
import './styles.css';

import React, { useContext } from 'react';
import { Navigation, Scrollbar } from 'swiper/modules';
import { SwiperSlide } from 'swiper/react';
import { useMediaQuery } from 'usehooks-ts';

import { CityContext } from '../../context/CityContext';
import { getDataList } from '../../utils/constants';
import CarouselBaseSwiper from '../carousel-base-swiper/CarouselBaseSwiper';
import { Scroll } from '../icons';
import Spinner from '../spinner/Spinner';
import Section from '../UI/section/Section';
import SectionTitle from '../UI/section-title/SectionTitle';
import { ALL_GALLERY_DATA_SAMARA } from './data/dataSam';
import { ALL_GALLERY_DATA_SPB } from './data/dataSpb';

interface ClubsGalleryProps {
  title: string;
}

interface SlideDataProps {
  name: string;
  arrayData: string[];
}

export default function ClubsGallery({ title }: ClubsGalleryProps) {
  const CITY_NAME = useContext(CityContext);
  const dataCity = getDataList(
    CITY_NAME,
    ALL_GALLERY_DATA_SAMARA,
    ALL_GALLERY_DATA_SPB,
  );
  const data = [dataCity].filter(val => val?.city === CITY_NAME)[0]?.list;

  const matches = useMediaQuery('(min-width: 567px)');

  const sliderMainData = data?.find(
    (el: SlideDataProps) => el.name === 'main',
  )?.arrayData;
  const sliderPoolData = data?.find(
    (el: SlideDataProps) => el.name === 'pool',
  )?.arrayData;
  const sliderGymData = data?.find(
    (el: SlideDataProps) => el.name === 'gym',
  )?.arrayData;
  const sliderSpaData = data?.find(
    (el: SlideDataProps) => el.name === 'spa',
  )?.arrayData;
  const sliderGroupData = data?.find(
    (el: SlideDataProps) => el.name === 'group',
  )?.arrayData;

  const breakpoints = {
    320: {
      slidesPerView: 1.2,
      freeMode: true,
      spaceBetween: 10,
    },
    640: {
      slidesPerView: 2,
    },
    768: {
      slidesPerView: 2.5,
      spaceBetween: 20,
    },
    1024: {
      slidesPerView: 3,
      spaceBetween: 30,
    },
  };
  const carouselProps = {
    navigation: true,
    slidesPerView: 3,
    modules: [Navigation],
  };
  const carouselProps2 = {
    slidesPerView: 3,
    scrollbar: true,
    modules: [Scrollbar],
  };

  return (
    <>
      <Section
        styleWrapper="bg-secondary-50 px-0 clubsGallery"
        styleInner="relative"
      >
        {title && (
          <SectionTitle as="h3" styles="px-4 xl:px-0 text-center md:text-left">
            {title}
          </SectionTitle>
        )}

        {!!data && [...data].length > 0 ? (
          matches ? (
            sliderMainData && [...sliderMainData].length > 0 ? (
              <CarouselBaseSwiper
                wrapperClass="main-clubs-slider"
                data={data}
                sliderId="main-clubs-slider"
                breakpoints={breakpoints}
                {...carouselProps}
              >
                {[...sliderMainData].map((el, index) => (
                  <SwiperSlide
                    key={index}
                    className="overflow-hidden rounded-xl"
                  >
                    <img
                      className="w-full h-full object-cover"
                      src={el}
                      alt=""
                    />
                  </SwiperSlide>
                ))}
              </CarouselBaseSwiper>
            ) : null
          ) : (
            <div className="-mt-9 md:mt-0">
              {sliderPoolData && [...sliderPoolData].length > 0 ? (
                <div className="relative">
                  <div className="p-4 pl-0 mb-1 mt-2 text-md font-semibold">
                    <span className="text-primary">  </span>
                        
                  </div>
                  <Scroll className="w-8 h-8 absolute top-5 right-0 text-accent" />
                  <CarouselBaseSwiper
                    wrapperClass="pool-clubs-slider mb-2"
                    data={data}
                    sliderId="pool-clubs-slider"
                    breakpoints={breakpoints}
                    {...carouselProps2}
                  >
                    {[...sliderPoolData].map((el, index) => (
                      <SwiperSlide
                        key={index}
                        className="overflow-hidden rounded-xl"
                      >
                        <img
                          className="w-full h-48 md:h-56 sm:h-full max-h-56 object-cover"
                          src={el}
                          alt=""
                        />
                      </SwiperSlide>
                    ))}
                  </CarouselBaseSwiper>
                </div>
              ) : null}

              {sliderGymData && [...sliderGymData].length > 0 ? (
                <div className="relative">
                  <div className="p-4 pl-0 mb-1 mt-2 font-semibold text-base">
                      
                    <span className="text-primary">
                      {' '}
                        {' '}
                    </span>
                  </div>
                  <Scroll className="w-8 h-8 absolute top-5 right-0 text-accent" />
                  <CarouselBaseSwiper
                    wrapperClass="gym-clubs-slider mb-2"
                    data={data}
                    sliderId="gym-clubs-slider"
                    breakpoints={breakpoints}
                    {...carouselProps2}
                  >
                    {[...sliderGymData].map((el, index) => (
                      <SwiperSlide
                        key={index}
                        className="overflow-hidden rounded-xl"
                      >
                        <img
                          className="w-full h-56 sm:h-full max-h-56 object-cover"
                          src={el}
                          alt=""
                          width="640px"
                          height="427px"
                        />
                      </SwiperSlide>
                    ))}
                  </CarouselBaseSwiper>
                </div>
              ) : null}

              {sliderSpaData && [...sliderSpaData].length > 0 ? (
                <div className="relative">
                  <div className="p-4 pl-0 mb-1 mt-2 text-md font-semibold">
                    <span className="text-primary">- </span>
                    <br />
                    , , ,    
                    
                  </div>
                  <Scroll className="w-8 h-8 absolute top-5 right-0 text-accent" />
                  <CarouselBaseSwiper
                    wrapperClass="spa-clubs-slider mb-2"
                    data={data}
                    sliderId="spa-clubs-slider"
                    breakpoints={breakpoints}
                    {...carouselProps2}
                  >
                    {[...sliderSpaData].map((el, index) => (
                      <SwiperSlide
                        key={index}
                        className="overflow-hidden rounded-xl"
                      >
                        <img
                          className="w-full h-56 sm:h-full max-h-56 object-cover"
                          src={el}
                          alt=""
                          width="640px"
                          height="427px"
                        />
                      </SwiperSlide>
                    ))}
                  </CarouselBaseSwiper>
                </div>
              ) : null}

              {sliderGroupData && [...sliderGroupData].length > 0 ? (
                <div className="relative">
                  <div className="p-4 pl-0 mb-1 mt-2 text-md font-semibold">
                      
                    <br />
                    <span className="text-primary">
                      , , , 
                    </span>
                  </div>
                  <Scroll className="w-8 h-8 absolute top-5 right-0 text-accent" />
                  <CarouselBaseSwiper
                    wrapperClass="spa-clubs-slider mb-2"
                    data={data}
                    sliderId="spa-clubs-slider"
                    breakpoints={breakpoints}
                    {...carouselProps2}
                  >
                    {[...sliderGroupData].map((el, index) => (
                      <SwiperSlide
                        key={index}
                        className="overflow-hidden rounded-xl"
                      >
                        <img
                          className="w-full h-56 sm:h-full max-h-56 object-cover"
                          src={el}
                          alt=""
                        />
                      </SwiperSlide>
                    ))}
                  </CarouselBaseSwiper>
                </div>
              ) : null}
            </div>
          )
        ) : (
          <div className="flex flex-col justify-center items-center">
            <Spinner />
          </div>
        )}
      </Section>
    </>
  );
}
