// import { useFormData } from './context';
import './styles.css';

import React, { useState } from 'react';
import { Step,Stepper } from 'react-form-stepper';

import { useMultyStepForm } from '../../hooks/useMultiStepForm';
import FormCompleted from './FormCompleted';
import StepOne from './StepOne';
import StepTwo from './StepTwo';

type FormData = {
  firstName: string;
  lastName: string;
  phone: string;
  email: string;
  birth_day: string;
  club_id: string;
  isCheck: boolean;
};

const initialState: FormData = {
  firstName: '',
  lastName: '',
  phone: '',
  email: '',
  birth_day: '',
  club_id: '',
  isCheck: false,
};

export default function Forms({ item }: any) {
  // const { setFormValues } = useFormData();
  const [data, setData] = useState<FormData>(initialState);

  const onSubmit = () => {
    next();
  };
  const { currentStepIndex, next, step, steps } = useMultyStepForm([
    <StepOne next={onSubmit} setData={setData} key={0} />,
    <StepTwo next={onSubmit} data={data} item={item} key={1} />,
    // item={item}
    <FormCompleted key={2} />,
  ]);
  return (
    <div className="w-full pt-12 pb-4 sm:py-10 md:py-0">
      <Stepper activeStep={currentStepIndex}>
        {[...steps].map((el, index) => (
          <Step label="" key={index} />
        ))}
      </Stepper>

      {step}
    </div>
  );
}
