import React, { useContext, useEffect, useState } from 'react';
import { NavLink, useNavigate } from 'react-router-dom';

import { CityContext } from '../../context/CityContext';
import { useModal } from '../../hooks/useModal';
import { CITIES_ROUTERS_SHORT } from '../../utils/routes';
import Modal from '../modal/Modal';
import ModalContent from '../modal/ModalContent';

export default function UserCity() {
  const navigate = useNavigate();
  const pageCity = useContext(CityContext);
  const [isOpen, setIsOpen] = useState<boolean>(false);
  const { closeModal, isModalOpen, openModal } = useModal();
  const localCity = sessionStorage.getItem('city');
  const classMenu =
    'flex flex-col justify-center gap-4 w-max z-10 absolute -bottom-36 left-1/2 -translate-x-1/2 px-8 py-5 shadow-xl rounded-lg bg-white uppercase text-center text-secondary font-bold';

  const onClickLinkYes = (): void => {
    setIsOpen(false);
    sessionStorage.setItem('city', pageCity);
  };

  const onClickLinkNo = (): void => {
    let userCity: {
      url: string;
      name: string;
    } = {
      url: '',
      name: '',
    };
    setIsOpen(true);
    if (CITIES_ROUTERS_SHORT.length > 2) {
      openModal();
      setIsOpen(false);
    } else {
      userCity = CITIES_ROUTERS_SHORT.filter(val => val.name !== pageCity)[0];
      sessionStorage.setItem('city', userCity?.name);
      navigate(userCity?.url);
    }
  };

  const onCloseModal = (): void => {
    closeModal();
    navigate(-1);
  };

  const onClickLinkCity = (e: React.MouseEvent<HTMLAnchorElement>): void => {
    sessionStorage.setItem('city', e.currentTarget.innerText);
    closeModal();
  };

  useEffect(() => {
    setIsOpen(false);

    if (!localCity) {
      setIsOpen(true);
    }
  }, [localCity]);

  return (
    <>
      <div className={`${classMenu} ${!isOpen ? 'hidden' : ''}`}>
        <h3 className="col-span-2 text-center">  {pageCity}?</h3>

        <div className="grid grid-cols-2 gap-4 w-full">
          <button
            className="uppercase p-4 mb-2 ml-auto text-center border-2 border-secondary rounded-lg bg-secondary hover:bg-white-50 text-white-50 hover:text-secondary w-24"
            onClick={onClickLinkYes}
          >
            
          </button>
          <button
            className="uppercase p-4 mb-2 text-center border-2 border-secondary rounded-lg hover:bg-secondary hover:text-white-50 w-24"
            onClick={onClickLinkNo}
          >
            
          </button>
        </div>
      </div>

      {isModalOpen && (
        <Modal onClose={onCloseModal} title="">
          <ModalContent>
            <h3 className="text-2xl font-bold mb-4 text-center w-full mt-8">
                
            </h3>
            <div className="grid sm:grid-cols-2 md:grid-cols-3 gap-2 py-4 pb-8 px-4">
              {CITIES_ROUTERS_SHORT?.map(val => (
                <NavLink
                  to={val.url}
                  key={val.url}
                  onClick={onClickLinkCity}
                  className="p-2 py-4 flex justify-center items-center text-md text-center border-2 border-secondary rounded-lg hover:bg-secondary bg-white-50 hover:text-white-50 text-secondary"
                >
                  {val.name}
                </NavLink>
              ))}
            </div>
          </ModalContent>
        </Modal>
      )}
    </>
  );
}
