import React, { useContext } from 'react';

import { CityContext } from '../../context/CityContext';
import { IClub, IProduct, ITariff } from '../../models/models';

import { getDataCity, getDataCityClubsTariff } from '../../utils';
import Spinner from '../spinner/Spinner';
import TariffItem from '../tariff-item/TariffItem';
import { useGetPriceCitiesQuery } from '../../store/reducers/pro.api';

export default function TariffsList() {
  const pageCity = useContext(CityContext);
  const { data } = useGetPriceCitiesQuery(pageCity, {
    refetchOnFocus: true,
  });
  const dataCityActive = getDataCity(data, pageCity);
  const clubs = dataCityActive?.clubs;
  const dataClubsProducts = clubs?.map((club: IClub) => {
    return club.products.filter(
      el => el.product_type !== 'subscription_recurrent',
    );
  })[0];
  const tariffData: ITariff[] = [];
  dataClubsProducts &&
    dataClubsProducts?.map((el: IProduct, i: number) =>
      tariffData.push({
        id: i + 1,
        type: el.product_type,
        period: el.product_type,
        title: el.product_name,
        price: el.product_price,
        clubs: [...getDataCityClubsTariff(clubs, el.product_type)],
      }),
    );

  return (
    <div className="w-full overflow-y-auto gap-8 flex flex-wrap sm:flex-nowrap justify-center md:py-4">
      {tariffData && tariffData.length > 0 ? (
        tariffData?.map(el => <TariffItem key={el.id} data={el} />)
      ) : (
        <Spinner />
      )}
    </div>
  );
}
