import React, { useContext } from 'react';

import { CityContext } from '../../context/CityContext';

import { ITariffClubsInfoProps } from '../../types';
import { getDataCity, getDataCityClubsTariff } from '../../utils';
import { useGetPriceCitiesQuery } from '../../store/reducers/pro.api';

export default function TariffClubsInfo({ type }: ITariffClubsInfoProps) {
  const pageCity = useContext(CityContext);
  const { isLoading, isError, data } = useGetPriceCitiesQuery(pageCity);
  const dataCityActive =
    !isError && !isLoading && !!data && getDataCity(data, pageCity);
  const clubs = dataCityActive.clubs;
  const TARIFF_TYPES = [
    {
      type: 'morning',
      text: '    1   17:00',
    },
    {
      type: 'full',
      text: '    1 ',
    },
    {
      type: 'mc_full',
      text: `     ${clubs.length} `,
    },
  ];

  return (
    <>
      {[...TARIFF_TYPES].map(
        item =>
          item.type === type && (
            <div
              key={item.type}
              className="my-2 md:my-4 opacity-70 text-sm md:text-base"
            >
              <p>{item.text}</p>
              <ul className="my-2 list-disc pl-4">
                {[...getDataCityClubsTariff(clubs, item.type)].map(
                  (club, index: number) => (
                    <li key={index}>{club.address}</li>
                  ),
                )}
              </ul>
            </div>
          ),
      )}
    </>
  );
}
