export const nowAgeUser = val => {
  var now = new Date(); // 
  var today = new Date(now.getFullYear(), now.getMonth(), now.getDate()); //   
  // var dob = new Date(1988, 6, 26); // 
  var dob = new Date(val);
  var dobnow = new Date(today.getFullYear(), dob.getMonth(), dob.getDate()); //   
  var age; //

  // =   -  
  age = today.getFullYear() - dob.getFullYear();
  //      ,    age  
  if (today < dobnow) {
    age = age - 1;
  }

  return age;
};

export const maxValueDate = () => {
  const nowDate = new Date().toLocaleDateString(); // 
  const m = nowDate.slice(3, 5);
  const d = nowDate.slice(0, 2);

  const y = new Date().getFullYear() - 14; // 14  
  const result = y + '-' + m + '-' + d;

  return result;
};

export const phoneWithoutFormatting = str => {
  return str.replace(/[^0-9]/g, '').slice(1).trim();
};

//     
const orderedCount = text => {
  return Array.from(
    text.split('').reduce((acc, el) => {
      acc.set(el, (acc.get(el) || 0) + 1);
      return acc;
    }, new Map()),
  );
};

export const checkPhone = phone => {
  const tel = phone.replace(/[^+\d]/g, '').slice(2);
  const num = tel.length;

  return num === 10 && orderedCount(tel)[0][1] < 10;
};

export const getDataCity = (data, cityName) => {
  return data && [...data].filter(el => el?.city === cityName)[0];
};

export const getDataCityClubs = arr => {
  return (
    !!arr &&
    [...arr.clubs].map(el => {
      return {
        name: el.name,
        address: el.address,
      };
    })
  );
};

export const getDataCityClubsTariff = (clubs, type) => {
  return clubs
    .map(club => {
      let flag = false;
      let idProduct = '';
      let recurrent = {};
      club.products.map(p => {
        if (p.product_type === type) {
          flag = !flag;
          idProduct = p.product_id;
        }
        if (p.product_type === 'subscription_recurrent') {
          recurrent = {
            id: p.product_id,
            name: p.product_name,
            price: p.product_price,
          };
        }
      });
      return flag
        ? {
            id: idProduct,
            recurrent: recurrent,
            name: club.name,
            address: club.address,
            pool: club.pool,
          }
        : null;
    })
    .filter(el => el !== null);
};

export const getIdVal = (arr, type) => {
  const el = [...arr].filter(el => el.product_type === type);
  return el[0].product_id;
};

export const getPriceVal = arr => {
  const el = [...arr].filter(
    el => el.product_type === 'subscription_recurrent',
  );
  return el[0].product_price;
};
