import React, { useContext } from 'react';

import { CityContext } from '../../context/CityContext';
import { ICityData, IClubsAddress } from '../../models/models';

import { getDataCity, getDataCityClubs } from '../../utils';
import { IconPin } from '../icons';
import { useGetPriceCitiesQuery } from '../../store/reducers/pro.api';

export default function ClubsAddress() {
  const pageCity = useContext(CityContext);
  const { data } = useGetPriceCitiesQuery(pageCity);
  const dataCityActive: ICityData = getDataCity(data, pageCity);
  const dataClub: IClubsAddress[] | any = getDataCityClubs(dataCityActive);

  return (
    <ul className="flex flex-wrap grow px-4 py-6 sm:py-0 md:max-w-screen-sm">
      {dataClub.length > 0 &&
        [...dataClub]?.map((el, index) => (
          <li
            key={index}
            className="flex grow text-white items-center w-full sm:w-1/2"
          >
            <IconPin width={20} height={26} className="w-4 h-5 md:w-5 md:h-7" />
            <span className="p-2 grow w-2/3">{el.address}</span>
          </li>
        ))}
    </ul>
  );
}
