import 'swiper/css/effect-fade';
import 'swiper/css/autoplay';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import './styles.css';

import React, { MouseEvent, useContext } from 'react';
import { Link } from 'react-router-dom';
import { Autoplay,EffectFade, Navigation, Pagination } from 'swiper/modules';
import { SwiperSlide } from 'swiper/react';

import { CityContext } from '../../context/CityContext';
import { ICarouselPromoProps } from '../../types';
import { getDataList } from '../../utils/constants';
import CarouselBaseSwiper from '../carousel-base-swiper/CarouselBaseSwiper';
import Spinner from '../spinner/Spinner';
import { PROMO_CAROUSEL_SAMARA } from './data/dataSam';
import { PROMO_CAROUSEL_SPB } from './data/dataSpb';

export default function CarouselPromo({ styles }: ICarouselPromoProps) {
  const CITY_NAME = useContext(CityContext);

  const dataCity = getDataList(
    CITY_NAME,
    PROMO_CAROUSEL_SAMARA,
    PROMO_CAROUSEL_SPB,
  );

  const data = [dataCity].filter(val => val?.city === CITY_NAME)[0]?.list;

  const breakpoints = {
    320: {
      slidesPerView: 1,
      freeMode: true,
    },
  };

  const carouselProps = {
    navigation: true,
    pagination: {
      clickable: true,
    },
    slidesPerView: 1,
    effect: 'fade',
    autoHeight: true,
    fadeEffect: {
      crossFade: true,
    },
    loop: true,
    autoplay: {
      delay: 2500,
      disableOnInteraction: false,
    },
    modules: [Navigation, Pagination, EffectFade, Autoplay],
  };

  const handleClick = (e: MouseEvent<HTMLAnchorElement>) => {
    const position = e.currentTarget?.href.indexOf('#');
    const el = e.currentTarget?.href.slice(position + 1);
    document
      .querySelector('[id="' + el + '"]')
      ?.scrollIntoView({ behavior: 'smooth' });
  };

  return (
    <>
      {!!data && [...data].length > 0 ? (
        <CarouselBaseSwiper
          wrapperClass={styles}
          data={data}
          sliderId="promo-slider"
          breakpoints={breakpoints}
          {...carouselProps}
        >
          {data &&
            [...data].map((el, index) => (
              <SwiperSlide
                key={index}
                className="flex flex-wrap items-end slide"
              >
                <div className="w-full sm:w-3/5 text-center sm:text-left p-4 sm:pl-20 sm:py-20">
                  {el?.content}
                  <Link
                    to={el.url}
                    className="inline-flex p-3 px-10 xl:px-20 font-bold text-xl bg-white-50 rounded-3xl"
                    style={{ backgroundImage: `url(${el})` }}
                    onClick={handleClick}
                  >
                    
                  </Link>
                </div>
                <div className="w-full h-auto mt-auto sm:w-2/5 x flex flex-col">
                  <div className="slide-pic">
                    <img
                      src={el?.bg}
                      alt=""
                      className="w-full h-full object-fill"
                      width="622"
                      height="641"
                      // loading="lazy"
                    />
                  </div>
                </div>
              </SwiperSlide>
            ))}
        </CarouselBaseSwiper>
      ) : (
        <div className="flex flex-col justify-center items-center">
          <Spinner />
        </div>
      )}
    </>
  );
}
