import React, { useContext } from 'react';

import { CityContext } from '../../context/CityContext';
import { data } from './data';

export default function OfertaCity() {
  const pageCity = useContext(CityContext);
  const content = data.filter(el => el?.city === pageCity);
  return (
    !!data && (
      <div className="py-3 text-white text-sm">{content[0]?.content}</div>
    )
  );
}
